import React from 'react'
import { connect } from 'react-redux'
import ReactBricks from 'react-bricks-infinite'
import '../../public/css/bricks.css'

const sizes = [
  {mq: '100px', columns: 6, gutter: 20},
];

const Brick = ({ id, image, shared, title, viewed, width, height }) => (
  <div key={id} className="briks-card card" style={{ height: `${height * 30 + 270}px` }}>
    <div className="innerDetails">
      <h6>{title}</h6>
      <img src={image} alt={title} />
      <p>shared: {shared}</p>
      <p>viewed: {viewed}</p>
    </div>
  </div>
)

const Bricks = ({ blocks = [], loadMoreBricks }) => (
  <div>
    <ReactBricks
      containerId="blocks-container"
      loadMoreBricks={loadMoreBricks}
      reRender={false}
      bricks={ blocks.map(Brick) }
      hasMoreBricks
      sizes={ sizes }
    />
  </div>
)

const BricksConnected = connect(
  ({ blocks: { blocks } }) => ({ blocks }),
  dispatch => ({
    loadMoreBricks: () => dispatch({ type: 'LOAD MORE BRICKS' }),
  }),
)(Bricks)

export default BricksConnected



