import React from 'react'
import { connect } from 'react-redux'

// From http://redux.js.org/docs/basics/UsageWithReact.html

const Counter = ({ count, increment, increment2, fetch, reset }) => (
  <div>
    <p>Value: {count}</p>
    <button onClick={increment}>Increment</button>
    <button onClick={increment2}>Increment2</button>
    <button onClick={fetch}>fetch</button>
    <button onClick={reset}>Reset</button>
  </div>
)


const CounterConnected = connect(
  ({ counter: { count } }) => ({ count }),
  dispatch => ({
    increment: () => dispatch({ type: 'INCREMENT' }),
    increment2: () => dispatch({ type: 'INCREMENT_ASYNC' }),
    fetch: () => dispatch({ type: 'FETCH', payload: {a: 10} }),
    test: () => dispatch({ type: 'TEST' }),
    reset: () => dispatch({ type: 'RESET' }),
  }),
)(Counter)


const About = () => (
  <div>
    <h1>This is what we're all about!</h1>
    <p>
      React, static sites, performance, speed. It's the stuff that makes us
      tick.
    </p>
    <h2>Here is a redux counter:</h2>
    <CounterConnected />
  </div>
)

export default About



