import React from 'react'
import Dropzone from 'react-dropzone'
import config from '../../static.config'

export default class DropZoneDemo extends React.Component {
  /*
   *   state = {
   *     sandwichOpened: false
   *   }
   *
   *   sandwichClicked = () => {
   *     this.setState({sandwichOpened: true})
   *   }
   *
   *   drawerChanged = (isOpened) => {
   *     this.setState({sandwichOpened: isOpened})
   *   }
   *  */

  onDrop = (acceptedFiles, rejectedFiles) => {
    console.log('onDrop:', acceptedFiles, rejectedFiles)
    console.log(acceptedFiles[0])
    // do stuff with files...

    let img = document.getElementById('droppedImage')

    var reader = new FileReader();
    reader.onload = (function (image) {
      return function (e) {
        image.src = e.target.result;
      }
    })(img)

    reader.readAsDataURL(acceptedFiles[0])
  }

  render = () => {

    return (
      <div>
        <h1>Drop zone demo</h1>
        <Dropzone onDrop={this.onDrop}>Drop any picture file here</Dropzone>

        <div>Last dropped picture:</div>
        <img id="droppedImage" alt="" />
      </div>
    )
  }

}



