import React, { Component } from 'react'
import { connect } from 'react-redux'
import '../../public/css/signUp.css'
import LoggedUser from './LoggedUser'
import config from "../../static.config";

const INITIAL_STATE = {
  email: '',
  password: '',
  loading: false,
  passwordConfirm: '',
  first: '',
  last: '',
  phone: '',
  emailSubscription: true,
}

class signUp extends Component {
  state = { ...INITIAL_STATE }
  isFormValid = () => {
    const {
      email,
      password,
      passwordConfirm,
      first,
      last,
      phone,
    } = this.state

    return email && password &&
      passwordConfirm === password &&
      first && last && phone
  }

  onSubmit = event => {
    event.preventDefault()
      this.setState({
        loading: true,
      });

    const {
      email,
      password,
      first,
      last,
      phone,
      emailSubscription,
    } = this.state
    if (this.isFormValid()) {
      this.props.emailPassSignUp(
        email,
        password,
        first,
        last,
        phone,
        emailSubscription
      );
    } else {
      this.setState({
          loading: false
      });

      alert('Please fill in all fields');
    }
  }

  render () {
    const { error } = this.props
    const isInvalid = !this.isFormValid()
    const { emailSubscription, loading } = this.state;


    return (
        loading ? <div>Loading ...</div> :
      <div>
        <div className="signup_area">
          <form onSubmit={ this.onSubmit }>
            <div className="clearfix">
              <h1>Sign Up</h1>
              <div className="single_signup">
                <label htmlFor="#">
                  First Name
                  <input type="text" onChange={({target: {value}}) => this.setState({first: value}) } />
                </label>
              </div>
              <div className="single_signup">
                <label htmlFor="#">
                  Last Name
                  <input type="text" onChange={({target: {value}}) => this.setState({last: value}) } />
                </label>
              </div>
              <div className="single_signup">
                <label htmlFor="#">
                  Email
                  <input type="email" onChange={({target: {value}}) => this.setState({email: value}) } />
                </label>
              </div>
              <div className="single_signup">
                <label htmlFor="#">
                  Phone
                  <input type="text" onChange={({target: {value}}) => this.setState({phone: value}) } />
                </label>
              </div>
              <div className="single_signup">
                <label htmlFor="#">
                  Password
                  <input type="password" onChange={({target: {value}}) => this.setState({password: value}) } />
                </label>
              </div>
              <div className="single_signup">
                <label htmlFor="#">
                  Confirm Password
                  <input type="password" onChange={({target: {value}}) => this.setState({passwordConfirm: value}) } />
                </label>
              </div>
            </div>
            <div className="subscript">
              { error ? <p className="login_error">{error.message}</p> : null }
              <div className="subscript_left">
                <label htmlFor="emailSubscription">
                  <input id="emailSubscription" type="checkbox" checked={ emailSubscription } onChange={() => this.setState({emailSubscription: !emailSubscription}) } />
                  <span>Subscribe to newsletter</span>
                </label>
              </div>
              <div className="subscript_right">
                <div className="sign_tp_button">
                  <button type="submit">Sign Up</button>
                </div>
              </div>
            </div>
          </form>
        </div>
        <LoggedUser />
      </div>
    )
  }
}

export default connect(
  ({ user: { error } = {} }) => ({ error }),
  dispatch => ({
    emailPassSignUp: (email, password, first, last, phone, emailSubscription) =>
      dispatch({ type: 'EMAIL_PASS_SIGNUP', payload: { email, password, first, last, phone, emailSubscription } }),
}),
)(signUp)



