import React, { Component } from 'react'
import { connect } from 'react-redux'
import { canUseDOM } from '../domutil'
import '../../public/css/login.css'
import '../../public/css/font-awesome.min.css'
import config from '../../static.config'
import LoggedUser from './LoggedUser'


const style = { background: `#000 url(${config.basePath}/img/login-background.png) no-repeat scroll center center / cover` }

const INITIAL_STATE = {
  email: '',
  password: '',
}

class logInArea extends Component {
  state = { ...INITIAL_STATE }

  onSubmit = event => {
    event.preventDefault()
    const { email, password } = this.state
    if (email && password) {
      this.props.emailPassLogin(email, password)
    }
  }

  redirectIfLoggedIn() {
    if (canUseDOM && this.props.user.isLoggedIn) {
      console.log('zzzzzzzzzzz', `${config.basePath}/news`)
      // window.location = `${config.basePath}/news`
    }
  }

  componentDidMount() {
    this.redirectIfLoggedIn()
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    this.redirectIfLoggedIn()
  }


  render () {
    const { user, facebookLogin, twitterLogin } = this.props
    const { email, password } = this.state
    const isInvalid = !email || !password

    const errorMessage = user.error ? user.error.message : null

    console.log('u:', user)
    return (
      <div>
        <div className="login_area">
          <div className="login_background" style={ style }>
            <h1>Log In</h1>
            <form onSubmit={ this.onSubmit }>
              <div className="single_login">
                <label htmlFor="email">
                  Login
                  <input id="email" type="text" onChange={({target: {value}}) => this.setState({email: value}) } />
                </label>
              </div>
              <div className="single_login">
                <label htmlFor="password">
                  Password
                  <input id="password" type="password" onChange={({target: {value}}) => this.setState({ password: value }) } />
                </label>
              </div>
              <div className="forgotten_password clearfix">
                <ul>
                  <li>Forgot Password?</li>
                  <li>Sign Up</li>
                </ul>
                <button disabled={isInvalid} type="submit">Log in</button>
                { errorMessage ? <p className="login_error">{errorMessage}</p> : null }

                <p style={{color: 'white'}}>Log in via testemail@gmail.com/111111 or buttons below</p>,
              </div>
            </form>
            <div className="social">
              <ul>
                <li>
                  <button onClick={facebookLogin}><i className="fa fa-facebook-f"></i></button>
                </li>
                <li>
                  <button onClick={twitterLogin}><i className="fa fa-twitter"></i></button>
                </li>
              </ul>
            </div>
          </div>
        </div>
        {/* <LoggedUser /> */}
      </div>
    )
  }
}

export default connect(
  ({ user }) => ({ user }),
  dispatch => ({
    emailPassLogin: (email, password) => dispatch({ type: 'EMAIL_PASS_LOGIN', payload: { email, password } }),
    facebookLogin: () => dispatch({ type: 'FACEBOOK_LOGIN' }),
    twitterLogin: () => dispatch({ type: 'TWITTER_LOGIN' }),
  }),
)(logInArea)



