import React from 'react'
import Drawer from 'react-motion-drawer';
import config from '../../static.config'

console.log('CC', config, config.basePath)

export default class ReactMotionDrawerDemo extends React.Component {
  state = {
    sandwichOpened: false
  }

  sandwichClicked = () => {
    this.setState({sandwichOpened: true})
  }

  drawerChanged = (isOpened) => {
    this.setState({sandwichOpened: isOpened})
  }

  render = () => {
    console.log('BP', config.basePath)
    return (
      <div>

        <img src={`${config.basePath}/img/sandwich.svg`} alt="sandwich.svg" style={{width: "50px", height: "50px"}} onClick={this.sandwichClicked}  />

        <span style={{"marginLeft": "50px", display: "inline"}}>Jump TV. Demo of react-motion-drawer. Press sandwich or swipe left edge</span>

        <Drawer open={this.state.sandwichOpened} onChange={this.drawerChanged} drawerStyle={{background: "lightgray"}} /*overlayColor="lightgray"*/>
        <ul /*style={{ opacity: "0.5" }}*/>
        <li>Plum</li>
        <li>Apple</li>
        <li>Orange</li>
          </ul>
        </Drawer>

      </div>
    )
  }
}



