import React from 'react'
import { connect } from 'react-redux'
import config from '../../static.config'
import '../../public/css/loggedUser.css'

const dummyImageSrc = `${config.basePath}/img/pro.png`
const barImageSrc = `${config.basePath}/img/bar.png`
const profileUrl = `${config.basePath}/profile`

//TODO: move to ../Components
const LoggedUser = ({ user, isLoggedIn, logOut }) => {
  if (isLoggedIn) {
    const { photoURL, displayName } = user
    const avatar = photoURL || dummyImageSrc

    return (
      <span className="h-profile">
        <a href={profileUrl} className="h-pro-wrap">
          <span className="pro-img"><img src={avatar} alt={displayName} /></span>
          <span className="pro-name carret-border">{displayName}</span>
        </a>
        {/* <a href={profileUrl} className="pro-bar"><img src={barImageSrc} alt="profile-dropdown" /></a> */}
        <button onClick={ logOut }>LogOut</button>
      </span>
    )
  }
  return (
    <span className="h-profile">
      {/* <span style={ { color: 'red' } }>User is not authorized</span> */}
      <a href={`${config.basePath}/login`}>Sign In</a>
    </span>
  )
}

export default connect(
  ({user: {user, isLoggedIn}}) => ({user, isLoggedIn}),
  dispatch => ({ logOut: () => dispatch({type: 'LOG_OUT'})})
)(LoggedUser)



