import React from 'react'
import { connect } from 'react-redux'
import ReactBricks from './ReactBricks'
import '../../public/css/bricks.css'

const sizes = [
  {mq: '100px', columns: 6, gutter: 20},
];

const NewsBrick = ({ image_url: image, id, publication_date: date, url, title, source }) => (
  <div key={id} className="briks-card card" style={{ height: '300px' }}>
    <a href={url}>
      <div className="innerDetails">
        <h6>{title}</h6>
        <img src={image} alt={title} />
        <p>Published: {new Date(date).toLocaleString()}</p>
        <p>source: {source}</p>
      </div>
    </a>
  </div>
)

const NuzzelComponent = ({ news, query, offset, count, searchNews, reRender }) => {
  news = news || []
  return (
    <div>
      <h2>Search:</h2>
      <input type="text" defaultValue="USA" onChange={({ target: { value } }) => { searchNews(value, 0, count) } } />
      <ReactBricks
        containerId="news-container"
        loadMoreBricks={() => searchNews(query, offset + 20, count)}
        reRender={ reRender }
        bricks={ news.map(NewsBrick) }
        hasMoreBricks={ news.length < 80 }
        useWindowForScroll={false}
        sizes={ sizes }
      />
    </div>
  )
}

const Nuzzel = connect(
  ({ nuzzel: { news, offset, count, query, reRender } }) => ({ news, offset, count, query, reRender }),
  dispatch => ({
    searchNews: (query, offset, count) => dispatch({ type: 'SEARCH NEWS', payload: { query, offset, count } }),
  }),
)(NuzzelComponent)

export default Nuzzel



