//import config from '../../static.config'
import { loadjs, loadcss, canUseDOM } from '../domutil'

//console.log('ZZ', firebase, firebaseui)

export default class LoginDemo extends React.Component {
  state = {
    loggedUser: null,
  }

  ui = null

  uiConfig = null
  /* sandwichClicked = () => {
   *   this.setState({sandwichOpened: true})
   * }

   * drawerChanged = (isOpened) => {
   *   this.setState({sandwichOpened: isOpened})
   * } */


  componentDidMount() {
    if (canUseDOM) {
      this.initFirebase()
    }
  }

  componentDidUpdate(prevProps, prevState) {
    // to init firebase on development mode
    //this.initFirebase()

    if (canUseDOM && !this.state.loggedUser && prevState.loggedUser) {
      // bad place
      // Initialize the FirebaseUI Widget using Firebase.
      //var ui = new firebaseui.auth.AuthUI(firebase.auth());

      // The start method will wait until the DOM is loaded.

      this.ui.start('#firebaseui-auth-container', this.uiConfig);

      firebase.auth().onAuthStateChanged(this.onAuthStateChanged)

      console.log(123)
    }
  }

  onAuthStateChanged = (user) => {
    this.setState({ loggedUser: user })
  }

  initFirebase() {

    Promise.resolve(
    ).then( () => loadjs('https://www.gstatic.com/firebasejs/5.0.3/firebase.js')
    ).then( () => loadjs('https://cdn.firebase.com/libs/firebaseui/3.0.0/firebaseui.js')
    ).then( () => loadcss('https://cdn.firebase.com/libs/firebaseui/3.0.0/firebaseui.css')
    ).then( () => {
      var config = {
        apiKey: "AIzaSyCfLjmvUPI2Y4-8YTnwAx6Jg9KCUUgW2D4",
        authDomain: "jump-tv.firebaseapp.com",
        databaseURL: "https://jump-tv.firebaseio.com",
        projectId: "jump-tv",
        storageBucket: "jump-tv.appspot.com",
        messagingSenderId: "616656212948"
      };
      firebase.initializeApp(config);


      this.uiConfig = {
        signInSuccessUrl: '#',  //<url-to-redirect-to-on-success>

        signInOptions: [
          // Leave the lines as is for the providers you want to offer your users.
          firebase.auth.GoogleAuthProvider.PROVIDER_ID,
          firebase.auth.FacebookAuthProvider.PROVIDER_ID,
          firebase.auth.TwitterAuthProvider.PROVIDER_ID,
          //firebase.auth.GithubAuthProvider.PROVIDER_ID,
          firebase.auth.EmailAuthProvider.PROVIDER_ID,
          //firebase.auth.PhoneAuthProvider.PROVIDER_ID
        ],

        // Terms of service url.
        //! tosUrl: '<your-tos-url>',

        //signInFlow: 'popup',
      };

      // Initialize the FirebaseUI Widget using Firebase.
      this.ui = new firebaseui.auth.AuthUI(firebase.auth());

      // The start method will wait until the DOM is loaded.
      this.ui.start('#firebaseui-auth-container', this.uiConfig);

      firebase.auth().onAuthStateChanged(this.onAuthStateChanged)
    })
  }

  logOutClicked = () => {
    firebase.auth().signOut().then(function() {
      // Sign-out successful.
      this.setState({ loggedUser: null })
    }).catch(function(error) {
      // An error happened.
    });
  }

  render = () => {
    let loggedUser = this.state.loggedUser
    console.log('U:', loggedUser)
    return (
      <div>
        <h1>Jump-TV login demo page</h1>

        { loggedUser
          ? [
            <p>Logged in as { loggedUser.displayName }</p>,
            <button onClick={this.logOutClicked}>Log out</button>
          ]
          : [
            <div id="firebaseui-auth-container"></div>,
            <p>Note: email login via dummy testemail@gmail.com/111111 account for demo purpoces</p>,
          ]
        }
      </div>
    )
  }
}



